
import Scroll from '../../../base/scroll/scroll'
import Step from '../../../base/step/step'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapGetters } from 'vuex'
export default {
name: "userQq",
components: {
Scroll,
XInput,
Group,
Step
},
watch: {
userMsg(val) {
if (val.length == 6) {
this.btnSaveState = true
return false
}
this.btnSaveState = false
},
userNewPhone(val) {
if (this.$refs.userNewPhone.valid) {
this.btnNewCodeState = true
}
},
userNewMsg(val) {
if (val.length == 6 && this.$refs.userNewPhone.valid) {
this.btnSureState = true
return false
}
this.btnSureState = false
},
},
computed: {
userPwdPhone: {
get: function() {
let reg = /^(\d{3})\d{4}(\d{4})$/;
let tel = this.userPhone.replace(reg, "$1^-^$2");
return tel
},
set: function(v) {
}
},

...mapGetters([
'userPhone'
])
},
data() {
return {
stepArray: ['验证身份', '绑定新手机'],
stepIndex: 0,
btnSaveState: false,
btnCodeState: true,
btnSureState: false,
btnNewCodeState: false,
userMsg: '',
userNewPhone: '',
userNewMsg: '',
disabled: '',
time: 60,
codeText: '获取',
timer: null,
chooseState: 1
}
},
methods: {

getCode(phone,state) {
this.$axios.post('/api/sms/send', {
telephone: phone,
type: 0
}).then((response) => {
if (response.data.code === '200') {
this.chooseState = state
if (state === 1) {
this.btnCodeState = false
} else {
this.btnNewCodeState = false
}
this.timer = setInterval(this.timeGo, 1000)
}else{
this.$vux.alert.show({
title: '错误提示',
content: response.data.message,
})
}
}).catch((error) => {
console.log(error)
this.btnCodeState = true
this.$vux.alert.show({
title: '错误提示',
content: '服务器错误',
})
})
},
timeGo() {
if (this.time >= 0) {
this.codeText = `${this.time}(s)`
this.time--
} else {
this.codeText = '获取'
this.time = 60
clearInterval(this.timer)
this.timer = null
if (this.chooseState === 1) {
this.btnCodeState = true
} else {
this.btnNewCodeState = true
}
}
},

checkCode(newMsg,phone,state){
if(state===1&&this.btnSaveState||state===2&&this.btnSureState){
this.$axios.post('/api/sms/verify', {
telephone: phone,
code: newMsg,
type: 0
}).then((response) => {
if (response.data.code !== '200') {
this.$vux.alert.show({
title: '错误提示',
content: response.data.message,
})
} else {
if(state===1){
this.stepIndex = 1
}else{
this.$vux.toast.show({
text:'修改成功',
type:'success',
time:1000,
onHide(){
_this.$router.push({ name: 'user' })
}
})
}
}
}).catch((error) => {
console.log(error)
})
}
}
}
}



